@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
header {
  text-align: center;
  background-color: var(--color-green);
  color: white;
}

header h1 {
  font: 700 2.5rem var(--font);
  padding: 60px 0 30px 0;
}

header p {
  padding: 0 10px 70px 10px;
  font: 400 1.3rem var(--font);
}

@media (min-width: 480px) {
  header h1 {
    font-size: 3.5rem;
  }
}

.searchForm {
  margin-bottom: 50px;
}

.searchForm input {
  display: block;
  padding: 10px;
  width: 100%;
  border: 1px solid var(--color-green);
}

.searchForm input::-webkit-input-placeholder {
  font: 400 1rem var(--font);
}

.searchForm input:-ms-input-placeholder {
  font: 400 1rem var(--font);
}

.searchForm input::-ms-input-placeholder {
  font: 400 1rem var(--font);
}

.searchForm input::placeholder {
  font: 400 1rem var(--font);
}

@media (min-width: 768px) {

  .searchForm input {
    max-width: 70%;
    margin: 0 auto;
  }

}
.results {
  min-height: 20vh;
}

.results li {
  padding: 10px;
  border: 1px solid var(--color-green);
  margin-bottom: 10px;
}

.results p {
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: 10px;
}

@media (min-width: 768px) { 
  .results {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: flex-start;
  }

  .results li {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(50% - 5px);
  }
}
.userNomsContainer {
  margin: 50px 0;
}

.userNomsContainer > p {
  padding: 10px 0 20px 0;
}

.userNoms li {
  border: none;
  background-color: var(--color-green);
  color: white;
}

.userNoms li button {
  background-color: white;
  font-weight: 500;
  color: var(--color-green);
  padding: 3px 8px;
}

.confirmBtn {
  width: 100%;
  margin-top: 20px;
  display: block;
} 

.nomsList {
  min-height: 56vh;
  padding-bottom: 50px;
}

p.nomsListYear {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .confirmBtn {
    width: 40%;
    margin: 10px auto 0 auto;
  }
}
.modal {
  width: 100%;
  color: white;
  background-color: var(--color-green);
  padding: 15px 10px;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.7);
  letter-spacing: 1.1px;
}

@media (min-width: 768px) {
  .modal {
    padding: 30px 10px;
  }
}
.footer {
  color: white;
  background-color: var(--color-green);
  text-align: center;
  padding: 15px;
  font-size: 0.9rem;
  letter-spacing: 1px;
}
.mainNav {
  padding: 50px 0;
  text-align: center;
}

.mainNav a {
  /* text-decoration: none; */
  padding: 15px;
  font-weight: 500;
  letter-spacing: 1.1px;
  color: #444;
}

.mainNav a:visited, .mainNav a:active {
  color: #444;
}
