* { box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

img {display: block; width: 100%;}

:root {
  --color-green: #004c3f;
  --font: Rubik, sans-serif;
}

body {
  font: 400 100% Rubik, sans-serif;
  font: 400 100% var(--font);
  color: #444;
}

.wrapper {
  max-width: 1000px;
  width: 90%;
  margin: 0 auto;
}

h2 {
  font: 500 1.3rem Rubik, sans-serif;
  font: 500 1.3rem var(--font);
  color: #004c3f;
  color: var(--color-green);
  margin-bottom: 5px;
}

h3 {
  font-weight: 500;
}

button {
  font: 400 0.9rem Rubik, sans-serif;
  font: 400 0.9rem var(--font);
  background-color: #004c3f;
  background-color: var(--color-green);
  border: none;
  color: white;
  padding: 5px 12px;
  letter-spacing: 1px;
}

button:disabled {
  opacity: 0.7;
}
